let utils = {
  /*  1
  
  javascript
  arg1arg2d
  Calc.Add(arg1,arg2,d)
  
  */
  add: function (arg1, arg2) {
    arg1 = arg1.toString(), arg2 = arg2.toString();
    var arg1Arr = arg1.split("."), arg2Arr = arg2.split("."), d1 = arg1Arr.length == 2 ? arg1Arr[1] : "", d2 = arg2Arr.length == 2 ? arg2Arr[1] : "";
    var maxLen = Math.max(d1.length, d2.length);
    var m = Math.pow(10, maxLen);
    var result = Number(((arg1 * m + arg2 * m) / m).toFixed(maxLen));
    var d = arguments[2];
    return typeof d === "number" ? Number((result).toFixed(d)) : result;
  },
  /*  2
  
  
  arg1arg2d
  Calc.Sub(arg1,arg2)
  
  */
  subtract: function (arg1, arg2) {
    return Calc.Add(arg1, -Number(arg2), arguments[2]);
  },
  /*  3
  
  
  arg1arg2d)
  Calc.Mul(arg1,arg2)
  
  */
  multiply: function (arg1, arg2) {
    var r1 = arg1.toString(), r2 = arg2.toString(), m, resultVal, d = arguments[2];
    m = (r1.split(".")[1] ? r1.split(".")[1].length : 0) + (r2.split(".")[1] ? r2.split(".")[1].length : 0);
    resultVal = Number(r1.replace(".", "")) * Number(r2.replace(".", "")) / Math.pow(10, m);
    return typeof d !== "number" ? Number(resultVal) : Number(resultVal.toFixed(parseInt(d)));
  },
  /*  4
  
  
  arg1arg2d)
  Calc.Div(arg1,arg2)
  arg1arg2
  */
  divide: function (arg1, arg2) {
    var r1 = arg1.toString(), r2 = arg2.toString(), m, resultVal, d = arguments[2];
    m = (r2.split(".")[1] ? r2.split(".")[1].length : 0) - (r1.split(".")[1] ? r1.split(".")[1].length : 0);
    resultVal = Number(r1.replace(".", "")) / Number(r2.replace(".", "")) * Math.pow(10, m);
    return typeof d !== "number" ? Number(resultVal) : Number(resultVal.toFixed(parseInt(d)));
  },
  /*  5
  
  
  
  
  
  */
  formatDate: function (date, fmt) {
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + "").substr(4 - RegExp.$1.length)
      );
    }
    let o = {
      "M+": date.getMonth() + 1,
      "d+": date.getDate(),
      "h+": date.getHours(),
      "m+": date.getMinutes(),
      "s+": date.getSeconds()
    };
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + "";
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1 ? str : utils.padLeftZero(str)
        );
      }
    }
    return fmt;
  },
  /*  6
  
  
  
  
  
  */
  padLeftZero: function (str) {
    return ("00" + str).substr(str.length);
  },
  /*  7
  
  
  stringstringsearchInputstringattributecss,valuecss
  
  
  */
  searchHighlight:function (string,searchInput,attribute,value) {
    let replaceReg=new RegExp(searchInput, 'ig');
    let replaceString=`<span style=${attribute+":"+value}>${searchInput}</span>`;
    return string.replace(replaceReg,replaceString)
  },
  /*  8
 Cookie
 
 
 
 
 */
  setCookie:function(name, value, expires, path, domain, secure) {
  let cookieText = encodeURIComponent(name) + '=' + encodeURIComponent(value);
  if (expires instanceof Date) {
    cookieText += '; expires=' + expires;
  }
  if (path) {
    cookieText += '; expires=' + expires;
  }
  if (domain) {
    cookieText += '; domain=' + domain;
  }
  if (secure) {
    cookieText += '; secure';
  }
  document.cookie = cookieText;
},
  /*  9
 Cookie
 
 
 
 
 */
  getCookie:function (name) {
  let cookieName = encodeURIComponent(name) + '=';
  let cookieStart = document.cookie.indexOf(cookieName);
  let cookieValue = null;
  if (cookieStart > -1) {
    let cookieEnd = document.cookie.indexOf(';', cookieStart);
    if (cookieEnd == -1) {
      cookieEnd = document.cookie.length;
    }
    cookieValue = decodeURIComponent(
      document.cookie.substring(cookieStart + cookieName.length, cookieEnd));
  }
  return cookieValue;
},
  /*  10
 Cookie
 
 
 
 
 */
  unsetCookie:function (name) {
  document.cookie = name + "= ; expires=" + new Date(0);
}
};
export default utils



